.navbar {
    width: 100%;
    font-family: "Poppins";
    background: transparent;

    .navbar-brand {
        color: #ffffff;
        font-weight: 700;
        font-size: 26px;
        line-height: 39px;
        letter-spacing: -0.04em;
    }

    .navbar-toggler {
        border: none;

        &:focus {
            box-shadow: none;
        }
    }

    .navbar-nav {
        .nav-item {
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;

            .nav-link {
                color: #ffffff;
            }
        }
    }
}

.navbar-fixed-top.scrolled {
    background: linear-gradient(99.48deg, rgba(16, 18, 31, 0.7) 38.93%, rgba(49, 29, 105, 0.7) 98.87%);
    backdrop-filter: blur(20px);
    transition: 200ms linear;
}

#modalNavbarMobile {
    .modal-dialog-scrollable {
        margin: 0;

        .modal-content {
            min-height: 100vh;
            border: none;
            border-radius: 0;
            background: linear-gradient(99.48deg, rgba(16, 18, 31, 0.7) 38.93%, rgba(49, 29, 105, 0.7) 98.87%);
            backdrop-filter: blur(20px);

            .modal-header {
                padding: 20px;
                border-bottom: none;

                .btn-close {
                    border-radius: unset;
                    padding: unset;
                    margin: unset;
                    background: url('../img/icon-close.png');
                    background-repeat: no-repeat;
                    width: 20px;
                    height: 20px;
                    opacity: unset;
                }

                h1 {
                    font-family: 'Poppins';
                    font-weight: 700;
                    font-size: 26px;
                    line-height: 39px;
                    letter-spacing: -0.04em;
                    color: #FFFFFF;
                }
            }

            .modal-body {
                padding: 20px;

                div {
                    .nav {
                        &:last-child {
                            margin-top: 50px;
    
                            .nav-link {
                                &:last-child {
                                    font-weight: 700;
                                }
                            }
                        }
    
                        .nav-link {
                            padding: 0;
                            font-family: 'Poppins';
                            font-weight: 500;
                            font-size: 24px;
                            line-height: 36px;
                            color: #FFFFFF;
                            padding-bottom: 16px;
                            border-bottom: 1px solid #9985B3;
    
                            &:first-child {
                                padding-top: 20px;
                            }
    
                            &:not(:first-child) {
                                padding-top: 16px;
                            }
                        }
                    }
                }

                #bottom {
                    padding-top: 80px;

                    span {
                        font-family: 'Poppins';
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 16px;
                        letter-spacing: 0.01em;
                        color: #E0DFEA;
                    }

                    .d-flex {
                        margin-top: 16px;

                        a {
                            margin-right: 17px;
                        }
                    }
                }

                .mail-to {
                    margin-top: 30px;

                    p {
                        font-family: 'Poppins';
                        font-weight: 500;
                        font-size: 16px;
                        line-height: 16px;
                        color: #9896AE;
                        margin-bottom: 10px;
                    }

                    a {
                        font-family: 'Poppins';
                        font-weight: 700;
                        font-size: 16px;
                        line-height: 24px;
                        letter-spacing: 0.02em;
                        text-decoration-line: underline;
                        color: #FFFFFF;
                    }
                }
            }
        }
    } 
}

@media (max-width: 574px) {
    #hero-req-quote .navbar {
        display: block;
    }

    #hero-req-quote .navbar
    .navbar {
        padding-top: 0 !important;
        padding-bottom: 0 !important;
        height: 80px;

        .navbar-nav {
            .nav-item {
                .nav-link {
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 500;
                    font-size: 24px;
                    line-height: 36px;
                }
            }
        }

        .container {
            padding: 20px;

            .navbar-brand,
            .navbar-toggler {
                padding-top: 20px;
                padding-bottom: 20px;
            }
        }
    }
}

@media (min-width: 575px) and (max-width: 767px) {
    .navbar {
        height: 80px;
        padding-top: 0;
        padding-bottom: 0;

        .nav-link {
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 500;
            font-size: 24px;
            line-height: 36px;
        }
    }
}

@media (min-width: 678px) and (max-width: 991px) {
    .navbar {
        height: 80px;
        padding-top: 0;
        padding-bottom: 0;

        .nav-link {
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
        }
    }
}
